import Vue from "vue";
import VueRouter from "vue-router";

// 商城
import center from "@/pages/shop/center/index.vue";
import lists from "@/pages/shop/lists/index.vue";
import cart from "@/pages/shop/cart/index.vue";
import index from "@/pages/shop/index/index.vue";
import goodsDetail from "@/pages/shop/goods_detail/index.vue";
import order from "@/pages/shop/my_order/index.vue";
import collect from "@/pages/shop/collect/index.vue";
import track from "@/pages/shop/track/index.vue";
import address from "@/pages/shop/add_address/index.vue";
import my_comment from "@/pages/shop/my_comment/index.vue";
import comment from "@/pages/shop/comment/index.vue";
import confirm_order from "@/pages/shop/confirm_order/index.vue";
import done_pay from "@/pages/shop/done_pay/index.vue";
import order_detail from "@/pages/shop/order_detail/index.vue";
import logistics from "@/pages/shop/logistics/index.vue";
import refund from "@/pages/shop/refund/index.vue";
import msg from "@/pages/shop/msg/index.vue";
import service from "@/pages/shop/service/index.vue";
import shop_lists from "@/pages/shop/shop_list/index.vue";

// 秒杀
import seckill_lists from "@/pages/seckill/lists/index.vue";
import seckill_index from "@/pages/seckill/index/index.vue";
import seckill_detail from "@/pages/seckill/detail/index.vue";

// 优惠卷
import coupon_center from "@/pages/coupon/center/index.vue";
import coupon_lists from "@/pages/coupon/lists/index.vue";
import coupon_get from "@/pages/coupon/get/index.vue";
import coupon_show from "@/pages/coupon/show/index.vue";
import dopay from "@/pages/coupon/dopay/index.vue";

//砍价
import haggle_index from "@/pages/haggle/index/index.vue";
import haggle_lists from "@/pages/haggle/lists/index.vue";
import haggle_detail from "@/pages/haggle/haggle_detail/index.vue";
import haggle_top from "@/pages/haggle/haggle_top/index.vue";
import haggle_instruction from "@/pages/haggle/instruction/index.vue";
import haggle_help from "@/pages/haggle/haggle_help/index.vue";

// 拼团
import collage_index from "@/pages/collage/index/index.vue";
import collage_lists from "@/pages/collage/lists/index.vue";

// 要告诉 vue 使用 vueRouter
Vue.use(VueRouter);

const routes = [
  {
    path: '/lists',
    name: 'lists',
    component: lists
  },
  {
    path: '/service',
    name: 'service',
    component: service
  },
  {
    path: '/center',
    name: 'center',
    component: center
  },
  {
    path: '/cart',
    name: 'cart',
    component: cart
  },
  {
    path: '/',
    name: 'index',
    component: index,
    meta: {keepAlive: true}
  },

  {
    path: '/goods_detail/:id',
    name: 'goods_detail',
    component: goodsDetail,

  },
  {
    path: '/order',
    name: 'order',
    component: order
  },
  
  {
    path: '/collect',
    name: 'collect',
    component: collect
  },
  {
    path: '/track',
    name: 'track',
    component: track
  },
  {
    path: '/address',
    name: 'address',
    component: address
  },
  {
    path: '/my_comment',
    name: 'my_comment',
    component: my_comment
  },
  {
    path: '/comment',
    name: 'comment',
    component: comment
  },
  {
    path: '/confirm_order/:id',
    name: 'confirm_order',
    component: confirm_order,

  },
  {
    path: '/order_detail/:id',
    name: 'order_detail',
    component: order_detail
  },
  {
    path: '/refund/:id',
    name: 'refund',
    component: refund
  },
  {
    path: '/logistics/:id',
    name: 'logistics',
    component: logistics
  },
  {
    path: '/msg',
    name: 'msg',
    component: msg
  },
  {
    path: '/done_pay/:id',
    name: 'done_pay',
    component: done_pay
  },
  {
    path: '/shop_lists/:id',
    name: 'shop_lists',
    component: shop_lists
  },

  {
    path: '/seckill/lists',
    name: 'seckill_lists',
    component: seckill_lists
  },
  {
    path: '/seckill/index/:id',
    name: 'seckill_index',
    component: seckill_index
  },
  {
    path: '/seckill/detail/:id',
    name: 'seckill_detail',
    component: seckill_detail
  },
  {
    path: '/haggle/index/:id',
    name: 'haggle_index',
    component: haggle_index
  },
  {
    path: '/collage/index/:id',
    name: 'collage_index',
    component: collage_index
  },

  {
    path: '/coupon/lists',
    name: 'coupon_lists',
    component: coupon_lists
  },
  {
    path: '/coupon/center',
    name: 'coupon_center',
    component: coupon_center
  },
  {
    path: '/haggle/lists',
    name: 'haggle_lists',
    component: haggle_lists
  },
  {
    path: '/collage/lists',
    name: 'collage_lists',
    component: collage_lists
  },
  {
    path: '/coupon/get/:id',
    name: 'coupon_get',
    component: coupon_get
  },
  {
    path: '/coupon/show',
    name: 'coupon_show',
    component: coupon_show,
    meta: {
      keepAlive: true,
      isBack: false
    }
  },
  {
    path: '/coupon/dopay/:id',
    name: 'dopay',
    component: dopay
  },


    {
        path: '/haggle/detail/:haggleid',
        name: 'haggle_detail',
        component: haggle_detail
    },
    {
        path: '/haggle/top/:orderid/:haggleid',
        name: 'haggle_top',
        component: haggle_top
    },
    {
        path: '/haggle/instruction/:eventinfoid',
        name: 'haggle_instruction',
        component: haggle_instruction
    },
    {
        path: '/haggle/help/:inviteuid/:haggleid',
        name: 'haggle_help',
        component: haggle_help
    },

]

var router = new VueRouter({
  // mode: 'history',
  routes
})
router.beforeEach((to, from, next) => {
  console.log('从:', from)
  console.log('到:', to)
  next();

  // 返回不刷新
  if(from.name == "coupon_lists" || from.name == "confirm_order") {
    to.meta.isBack = true
  }

})

export default router;